<script>
import axios from 'axios'

    export default {
        data(){
            return {
                isShow: true,
                active: 0,
                field: '',

                isdisabled:true,
                item:"",
                checkBoxList:[],

                InitRecDTO: {                    
                    userInitField: '',
                    userInitTecList: [],
                    userId:null
                },

                userName: '',
                userEmail: '',
                userId: '',
                password:'',
            }
        },
        mounted(){
            let userId = this.$cookie.get('cookieUserId')
            console.log("userId from cookie:",userId)
            if (userId==null || userId == '') {
                //用户通过非正常手段进入该页面
                this.isShow = false
                this.$alert('不可以直接访问该页面哦!', '警告', {
                    confirmButtonText: '回去注册',
                    callback: action => {
                        window.location.href='/login/register'
                    }
                });
            }else{
                console.log("show page and getUserRegisterInfo")
                this.isShow = true
                this.getUserRegisterInfo()
            }
        },
        methods:{
            handleButtonClick(item){
                this.item=''
                this.item=item
                console.log("单选:"+this.item)
            },
            nextStep(){
                if(this.active<2){
                    console.log(this.item,this.checkBoxList)
                    //还没到最后一页
                    //需要判断有没有填写内容
                    if(this.active==0){
                        if(this.item==''){
                            // 没有写值
                            this.$message({
                                message: '请选择你喜欢的领域!',
                                type: 'warning'
                            });
                        }else{
                            this.active++
                        }
                    }
                    if(this.active==1){
                        if(this.checkBoxList.length<1){
                            this.$message({
                                message: '请选择你喜欢的技术!',
                                type: 'warning'
                            });
                        }else{
                            this.active++
                        }
                    }

                    if(this.active!=0){
                        this.isdisabled=false
                    }
                }
            },
            preStep(){
                if(this.active>0){
                    this.active--;
                }else{
                    this.isdisabled = true
                }
            },
            submit(){
                this.InitRecDTO.userId = this.$cookie.get('cookieUserId')
                this.InitRecDTO.userInitField = this.item
                this.InitRecDTO.userInitTecList = this.checkBoxList
                //提交信息
                let that = this
                console.log("正在执行提交操作.", JSON.stringify(this.InitRecDTO))
                axios.post('/api/rec/requireInitRec',this.InitRecDTO)
                .then((res)=>{
                    if(res.data.code == 100){
                        that.$alert('恭喜提交成功!', '感谢', {
                            confirmButtonText: '回到主页',
                            callback: action => {
                                window.location.href='/'
                            }
                        });
                    }else{
                        that.$message.error("服务繁忙!")
                    }
                })
                .catch((error)=>{
                    that.$message.error("未知异常!")
                })
            },
            getUserRegisterInfo(){
                let userId = this.$cookie.get('cookieUserId')
                let that = this
                axios.post(`/api/user/getUserById/${userId}`)
                .then((res)=>{
                    that.userId = res.data.object.userId
                    that.userEmail = res.data.object.email
                    that.userName = res.data.object.userName
                    that.password = res.data.object.password
                })
            }
        }
    }
</script>

<template>
    <div v-if="isShow" style="margin: 0px;font-size: 0px; border: 0px solid red">
        <div class="header">
            <div class="mainwarp">
                <div class="logo"><a href="/"><img style="height: 50px; width: 50px;" src="/static/images/MYHD.png" /></a></div>
                <div class="welcome">欢迎注册</div>
                <div class="headerright">
                    <a href="/login">个人登录</a>
                    <span>|</span>
                    <a href="/">返回首页</a>
                </div>
            </div>
        </div>

        <div style="background-color: white; overflow: hidden;">
            <div style="text-align: center;">
                <div style="font-size: 20px; font-weight: bolder;">恭喜你,已经成功完成注册,现在让我们来进行推荐的初始化设置!</div>
            </div>
            <div style="display: flex; justify-content: center; margin-top: 10px;">
                <div style="width: 55%;">
                    <el-steps :active="active" finish-status="success">
                        <el-step title="领域" description="选择你喜欢的领域"></el-step>
                        <el-step title="技术" description="选择你喜欢的技术"></el-step>
                        <el-step title="提交" description="提交已经填写完的信息"></el-step>
                    </el-steps>
                </div>
            </div>
            <div style="display: flex; margin-top: 5px">
                <div style="width: 55%; 
                    height: 535px; 
                    border: 1px solid #DCDFE6;
                    margin-left: 20%;
                    display: flex;
                    flex-direction: column;">
                    <!-- 分割线上部分 active = 0  -->
                    <div style="border: 0px solid red; height: 400px;"
                        v-if="active==0"
                        >
                        <div style="font-size: 30px; font-weight: bolder;
                            text-align: center;
                            color: #E6A23C;
                            margin-top: 10px
                            ">
                            选择你的领域
                        </div>
                        <div style="border:0px solid red;
                            width: 75%;
                            height: 70%;
                            margin-top: 5%;
                            margin-left: 11%;
                            display: flex;
                            
                            flex-direction: column
                            ">
                            <div style="height: 50%; width: 100%; ;border: 0px solid red; display: flex; flex-direction: row;">
                                <el-button type="primary" plain
                                    style="width: 30%;
                                    margin-left: 5%;"
                                    @click="handleButtonClick('IT|互联网')"
                                    
                                    >IT|互联网
                                </el-button>
                                <el-button type="primary" plain
                                    style="width: 30%;
                                    margin-left: 5%;"
                                    @click="handleButtonClick('新能源')"
                                    
                                    >新能源
                                </el-button>
                                <el-button type="primary" plain
                                    style="width: 30%;
                                    margin-left: 5%;"
                                    @click="handleButtonClick('生产与制造')"
                                    
                                    >生产与制造
                                </el-button>
                            </div>
                            <div style="height: 50%; width: 100%; ;border: 0px solid red; display: flex; flex-direction: row; margin-top: 3%;">
                                <el-button type="primary" plain
                                    style="width: 30%;
                                    margin-left: 5%;"
                                    @click="handleButtonClick('外贸|贸易')"
                                                       
                                    >外贸|贸易
                                </el-button>
                                <el-button type="primary" plain
                                    style="width: 30%;
                                    margin-left: 5%;"
                                    @click="handleButtonClick('销售|市场营销')"
                                    
                                    >销售|市场营销
                                </el-button>
                                <el-button type="primary" plain
                                    style="width: 31%;
                                    margin-left: 5%;"
                                    @click="handleButtonClick('新能源|生产与制造|嵌入式')"
                                
                                    >新能源|生产与制造|嵌入式
                                </el-button>
                            </div>
                        </div>

                        <div style="font-size: 11px; 
                            display: flex;
                            justify-content: center;
                            margin-top: 10px
                            "
                            >已选择:{{ this.item }}
                        </div>
                        
                    </div>

                    <div style="border: 0px solid red; height: 400px;"
                        v-if="active==1"
                        >
                        <div style="font-size: 30px; font-weight: bolder;
                            text-align: center;
                            color: #E6A23C;
                            margin-top: 10px
                            ">
                            选择你喜欢的技术
                        </div>
                        <div style="border:0px solid red;
                            width: 75%;
                            height: 70%;
                            margin-top: 5%;
                            margin-left: 11%;
                            display: flex;
                            
                            flex-direction: column
                            ">
                            <div style="height: 100%; width: 100%; ;border: 0px solid red; display: flex; flex-wrap: wrap;">
                                <el-checkbox-group v-model="checkBoxList" size="medium">
                                    <el-checkbox class="checkBox"  label="Java" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="Go" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="Python" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="C++" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="网络安全" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="运维" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="实施" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="测试" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="DBA" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="ios" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="android" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="php" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="lua" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="人工智能" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="算法" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="vue" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="react" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="小程序" border></el-checkbox>
                                    <el-checkbox class="checkBox"  label="数控" border></el-checkbox>
                                </el-checkbox-group>
                            </div>
                        </div>

                        <div style="font-size: 11px; 
                            display: flex;
                            justify-content: center;
                            margin-top: 10px
                            "
                            >已选择:{{ JSON.stringify(this.checkBoxList) }}
                        </div>
                    </div>

                    <div style="border: 0px solid red; height: 400px;"
                        v-if="active==2"
                        >
                        <div style="font-size: 30px; font-weight: bolder;
                            text-align: center;
                            color: #67C23A;
                            margin-top: 10px
                            ">
                            感谢!系统已经完成了初步的过滤推荐
                        </div>
                        <div style="font-size: 20px; font-weight: bold; justify-content: center; display: flex;
                            margin-top: 10px;
                            color: #909399;
                            ">
                            提交信息,并进入系统吧!
                        </div>
                        <el-result icon="success" title="成功" subTitle="信息已经收集,请牢记如下你的注册信息">
                        </el-result>
                        <div style="font-size: 13px; 
                            display: flex; 
                            justify-content: center;
                            margin-top: -10px;
                            "
                        >
                            <el-descriptions :column="4" size="small" border>
                                <el-descriptions-item>
                                    <template slot="label">
                                        <i class="el-icon-user"></i>
                                        用户名
                                    </template>
                                    {{ this.userName }}
                                </el-descriptions-item>
                                <el-descriptions-item>
                                    <template slot="label">
                                        <i class="el-icon-message"></i>
                                        邮 箱     
                                    </template>
                                    {{ this.userEmail }}
                                </el-descriptions-item>
                                <el-descriptions-item>
                                    <template slot="label">
                                        <i class="el-icon-postcard"></i>
                                        用户号
                                    </template>
                                    {{ this.userId }}
                                </el-descriptions-item>
                                <el-descriptions-item>
                                    <template slot="label">
                                        <i class="el-icon-unlock"></i>
                                        密码
                                    </template>
                                    {{ this.password }}
                                </el-descriptions-item>
                            </el-descriptions>
                        </div>
                    </div>
                    

                    <!-- 分割线 -->
                    <el-divider>我是分割线</el-divider>
                    <!-- 分割线下部分 -->
                    <div style="display: flex;
                        flex-direction: row;
                        margin-left: 15%;
                        margin-top: 10px;
                        ">
                        <el-button :disabled=isdisabled @click="preStep" style="margin-left: 2%;">上一步</el-button>
                        <el-button v-if="active!=2" @click="nextStep" style="margin-left: 60%;">下一步</el-button>
                        <el-button v-if="active==2" type="primary" @click="submit" style="margin-left: 60%;">提交信息</el-button>
                    </div>

                </div>
            </div>
        </div>

    </div>
</template>

<style>
.checkBox{
    margin-left: 10px;
    margin-top: 10px;
}
</style>